                    <div id="divPopNotificationData" class="position-absolute border themecontrol d-none">
                       <Unreadnotification v-if="notifications" :notifications="notifications"></Unreadnotification>
                    </div>
                </li>
                <li class="nav-item">
                    <a class="nav-link ancbookmark"
                       :title="$t('Bookmark')"
                       href="javascript:;"  @click="OnShowBookMark">
                        <em class="fa fa-star"></em><span class="bookmark text-lmenu">Bookmark</span>
                    </a>
                </li>
                <li class="nav-item d-none">
                    <a class="nav-link" :title="$t('Socialize')" href="javascript:;">
                        <em class="fa fa-comments"></em>
                        <span class="socialize">Socialize</span>
                    </a>
                </li>
                <li>
                    <RouterLink to="/User/Home"
                                class="nav-link sidebaricon"
                                checkPrivilege="Yes">
                        <span>{{ $t("Dashboard") }}</span>
                    </RouterLink>
                </li>
                <li class="nav-item dropdown position-unset">
                    <a class="nav-link dropdown-toggle sidebaricon position-relative"
                      :title="$t('FullMenu')"
                       href="javascript:;"
                       data-toggle="dropdown"
                       id="navbarDropdownPortfolio"
                       aria-haspopup="true"
                       aria-expanded="false"
                       >
