                    </a>
                    <div v-bind:class="{'custom-width-menu':TotalCount<=5}" class="dropdown-menu dropdown-menu-right full-menu-new overflow-visible mt-0" id="fullMenuUl" aria-labelledby="navbarDropdownPortfolio" style="max-height:1000px;">
                        <div class="menu-search-panel">
                            <input type="text" name="search" value="" @keyup="SeacrchMenuItem($event)" class="menusearchitem" placeholder="Type to Search Menu items" />
                            <a class="clear-search" href="javascript:;" title="@Resources.Clear"><em class="fa fa-times"></em></a>
                        </div>
                        <div class="navigation-panel w-100 navigation-panel-full-menu">
                            <div class="nav-heading" style="display:none;">
                                <span><em class="fa fa-home cursor-pointer text-dark pr-2 ancbacktomenu"></em></span>
                                <span><em class="fa  fa-angle-right text-dark pr-2"></em></span>
                                <span id="spntext"><em class="fa fa-th-list d-none"></em></span>

                                <span class="back ml-auto"><span id="spnAdvancedSetup" v-if="UserType=='CA'"><em class="fa fa-th-list d-none"></em> </span><a class="cursor-pointer ancbacktomenu" title="Back"><em class="fa fa-chevron-left"></em>Back</a></span>
                            </div>
                            <perfect-scrollbar>
                            <div id="scrollbar" style="max-height:350px!important;">                              
                                <ul v-for="(parent,parentIndex) in MenuDataGroupBy" :key="parentIndex + Math.random()">
                                    <li v-for="(item,itemIndex) in parent.Menu" :key="itemIndex + Math.random()" :data-id="item.ModuleMasterId" :data-name="item.ModuleName" :data-url="item.ShowDashboardLink == 0 ? '' : '/'+item.ControllerName+'/'+item.ActionName">
                                       
                                        <a :class="{'mainmenu menuitemsover cursor-pointer': true, 'disabled': item.IsDisabled == 0 }" href="javascript:;"
                                        :checkPrivilege="item.RoleApplicable"
                                           v-if="(item.ShowDashboardLink == 1)">
                                            <em :class="'fa '+item.ModuleIcon"></em>{{item.ModuleName}}
                                            <span class="fa  fa-angle-right gotosub" @click="GoToSubCategory($event,item.ModuleMasterId)"></span>
                                        </a>
                                        <a :class="{'mainmenu menuitemsover cursor-pointer menudashboard': true}" href="javascript:;"
                                           v-if="item.ShowDashboardLink == 0">
                                            <em :class="'fa '+item.ModuleIcon"></em>{{item.ModuleName}}
                                            <span class="fa  fa-angle-right gotosub" @click="GoToSubCategory($event,item.ModuleMasterId)"></span>
                                        </a>
                                    </li>
                                </ul>
                               
                                <div class="w-100 float-left" v-for="(parent,parentIndex1) in MenuDataList" :key="parentIndex1+ Math.random()">
                                    
                                    <ul :class="'submenu d-none clschild_'+parent.ModuleMasterId" :data-p-id="parent.ModuleMasterId" :data-p-name="parent.ModuleName" 
                                    v-for="(url,urlIndex ) in parent.UrlList" :key="urlIndex + Math.random()">
                                        <li v-for="(item,itemIndex) in url.URL" :key="itemIndex + Math.random()" class="menuitem">
                                            <RouterLink :class="{'disabled': item.IS_DISABLED == 0}" v-if="getRootLink(item) == 'CURRENT'" :to="'/'+item.CONTROLER_NAME+'/'+item.ACTION_NAME+'?reqFrom='+parent.ModuleMasterId" >
                                                <em class="fa fa-circle"></em>{{($t(item.DISPLAY_NAME))}}
                                            </RouterLink>
                                            <a v-else  :class="{'disabled': item.IS_DISABLED == 0}" :href="getRootLink(item) + '/' + item.CONTROLER_NAME+'/'+item.ACTION_NAME+'?reqhFrom='+parent.ModuleMasterId">
                                                <em class="fa fa-circle"></em>{{($t(item.DISPLAY_NAME))}}
                                            </a>
                                        </li>
                                    </ul>
                                </div>                        
                            </div>
                        </perfect-scrollbar>
                        </div>
                    </div>
                </li>
                <li class="nav-item dropdown">
                    <a class="nav-link dropdown-toggle sidebaricon"
                       title="" :href="ProfileUrl"
                       id="navbarDropdownMenuLink"
                       data-toggle="dropdown"
                       data-bs-auto-close="outside"
                       aria-haspopup="true"
                       aria-expanded="false"
                       data-toggle-tooltip="tooltip"
                       data-original-title="My Profile">
                        <span class="my-image d-none d-lg-block img-lmenu"
                              style="line-height: normal">
                            <img :src="Avatar"
                                 alt="Image"
                                 onerror="this.src = '/Content/images/DefaultImages/avatar-icon.jpg'"
                                 loading="lazy" />
                        </span>
                        <em class="fa fa-user i-lmenu"></em><span>{{ GetUserInfo.Name.split(" ")[0] }}</span>
                    </a>
                    <ul class="dropdown-menu dropdown-menu-right my-account"
                        aria-labelledby="navbarDropdownMenuLink" data-parent="#navbarDropdownMenuLink">
                        <li>
                            <a class="dropdown-item"
                               :href="ProfileUrl"
                               title=""
                               data-toggle-tooltip="tooltip"
                               data-original-title="My Profile"><em class="fa fa-id-card"></em>My Profile</a>
                        </li>
                        <li>
                            <input type="hidden" id="hdnUserLanguage" value="en">
                            <a class="dropdown-item dropdown-toggle" id="dropdownMenuButtonLanguage" data-toggle="dropdown" aria-expanded="false" href="javascript:;"><em class="fa fa-globe"></em>{{SelectedLanguage}}</a>
                                <vue-scroll class="dropdown-menu selectlanguage" aria-labelledby="dropdownMenuButtonLanguage">
                                <ul>
                                    <li v-for="(lang, i) in Languages" :key="i + Math.random()"><a class="dropdown-item" href="javascript:;" @click="SetLanguage(lang.LANGCODE)" :typefor="lang.LANGCODE"><span :class="'flag-icon flag-icon-squared mr-1 flag-icon-'+lang.LANG_ICON"></span>{{ lang.LANG_NAME }}</a></li>
                                </ul>
                               </vue-scroll>
                        </li>
                        <li>
                            <a class="dropdown-item dropdown-toggle" id="dropdownMenuButtonMenuType" href="javascript:;" data-toggle="dropdown" aria-expanded="false">
                                <em class="fa fa-bars"></em>Menu :<span id="spnMenuType">{{SelectedMenuType}}</span>
                            </a>
                            <ul class="dropdown-menu" aria-labelledby="dropdownMenuButtonMenuType">
                                <li>
                                    <a class="dropdown-item sidebarCollapse"
                                       href="javascript:;" @click="SetMenuType('T')">Top</a>
                                </li>
                                <li>
                                    <a class="dropdown-item sidebarCollapse"
                                       href="javascript:;" @click="SetMenuType('L')">Left</a>
                                </li>
                            </ul>
                        </li>
                        <li>
                            <a class="dropdown-item dropdown-toggle" href="javascript:;">
                                <em class="fa fa-table"></em>Theme :
                                <span id="spnTheme" valuefor="CssGreen">Default</span>
                            </a>
                            <ul class="dropdown-menu themeselect">
                                <li>
                                    <a class="dropdown-item"
                                       href="javascript:;"
                                       title=""
                                       valuefor="Colored"
                                       data-toggle-tooltip="tooltip"
                                       data-original-title="Colored">Colored</a>
                                </li>
                                <li>
                                    <a class="dropdown-item"
                                       href="javascript:;"
                                       title=""
                                       valuefor="Dark"
                                       data-toggle-tooltip="tooltip"
                                       data-original-title="Dark">Dark</a>
                                </li>
                                <li>
                                    <a class="dropdown-item"
                                       href="javascript:;"
                                       title=""
                                       valuefor="Default"
                                       data-toggle-tooltip="tooltip"
                                       data-original-title="Default">Default</a>
                                </li>
                                <li>
                                    <a class="dropdown-item"
                                       href="javascript:;"
                                       title=""
                                       valuefor="Light"
                                       data-toggle-tooltip="tooltip"
                                       data-original-title="Light">Light</a>
                                </li>
                            </ul>
                        </li>
                        <li>
                            <a class="dropdown-item"
                               href="https://app.talygen.com/Notification/Index"
                               title=""
                               data-toggle-tooltip="tooltip"
                               data-original-title="Notifications"><em class="fa fa-bell"></em>Notifications</a>
                        </li>
                        <li>
                            <a class="dropdown-item"
                               href="https://app.talygen.com/Help/TalygenHelp"
                               title=""
                               data-toggle-tooltip="tooltip"
                               data-original-title="Help"><em class="fa fa-question"></em>Help</a>
                        </li>
                        <li>
                            <a class="dropdown-item"
                               id="DashboardPolicy"
                               href="javascript:;"
                               title=""
                               data-toggle-tooltip="tooltip"
                               data-original-title="Policy"><em class="fa fa-wpforms"></em>Policy</a>
                        </li>
                        <li>
                            <RouterLink to="/logout" class="dropdown-item" checkPrivilege="Yes" data-original-title="Logout"><em class="fa fa-power-off"></em>Logout</RouterLink>
                           
                        </li>
                    </ul>
                </li>
            </ul>
        </div>
        <input type="hidden" id="hdnSortExp" value="" />
    <input type="hidden" id="hdnSortBy" value="" />
    <input type="hidden" id="hdnChkMobile" value="no" />
    <input type="hidden" id="hdnischildsearch" value="0" />
    <!-- <input type="hidden" id="hdnChatUrl" value=" /mychat/" /> -->
    <Choosewizards v-if="showWizard" v-on:close="showWizard=false"></Choosewizards>
    </div>   
</template>
<script>
// import '../../../Content/Responsive/flags/flag-icon.min.css';
import $ from "jquery";
import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "../../stores/NavUserInfo";
const currentAppRootURL = process.env.VUE_APP_CURRENT_SITE_ROOT_URL;
const defaultAppURL = process.env.VUE_APP_DEFAULT_APP_URL;
const fullMenuRedirectionApplied = process.env.VUE_APP_FULL_MENU_APP_REDIRECTION;
import DataService from "../../services/DataService"       
import Unreadnotification from "../Notification/Unreadnotification.vue";
import Choosewizards from "../Wizard/choosewizards.vue";
import EN from '../../localization/en'
    export default {
    components: { Unreadnotification, Choosewizards},
    i18n: {
        locale: "en",
        messages: {
            en: EN,
        },
    },
    data() {
        return {
            MenuData: [],
            MenuDataList: [],
            MaxLoop: 5,
            ModuleName: "",
            MenuDataGroupBy: [],
            MenuCountmodule: 0,
            TotalCount: 0,
            TotalMenuCount: 0,
            Languages: [],
            SelectedLanguage: "English",
            SelectedMenuType: "Top",
            notifications:null,
            showWizard:false,
            showBookmark:false,
            CompanyLogo:null,
            Avatar:null,
            ProfileUrl:null,
            UserType:null
        };
    },
    setup() {
        const store = useloggedUserInfo();
        const { GetUserInfo } = storeToRefs(store);
        return { GetUserInfo };
    },
    created:async function () {
        this.CompanyLogo=this.GetUserInfo.CompanyLogo;
        this.UserType=this.GetUserInfo.UserType;
        this.Avatar=this.GetUserInfo.Avatar;
        this.Avatar=this.Avatar?this.Avatar:'/Content/images/DefaultImages/avatar-icon.jpg';
        if(!this.CompanyLogo || this.CompanyLogo.toLowerCase() == "company-logo.jpg")
                    { 
                        this.CompanyLogo='/Content/images/DefaultImages/company-logo.jpg';
                    }else{
                        await this.GetCompanyLogo(this.CompanyLogo);
                    }        
        this.ProfileUrl='https://app.talygen.com/MyProfile/Index?companyid='+this.GetUserInfo.CompanyId;
        this.GetFULLMenuData();
        this.GetLanguageList();
    },
    methods: {
        OnShowBookMark(){
            const customEvent = new CustomEvent('event_addbookmark')
            window.dispatchEvent(customEvent);
        },
        toggleFullscreen(elem) {
        elem = elem || document.documentElement;
        if (!document.fullscreenElement && !document.mozFullScreenElement &&
          !document.webkitFullscreenElement && !document.msFullscreenElement) {
            if (elem.requestFullscreen) {
                elem.requestFullscreen();
            } else if (elem.msRequestFullscreen) {
                elem.msRequestFullscreen();
            } else if (elem.mozRequestFullScreen) {
                elem.mozRequestFullScreen();
            } else if (elem.webkitRequestFullscreen) {
                elem.webkitRequestFullscreen(Element.ALLOW_KEYBOARD_INPUT);
            }
        } else {
            if (document.exitFullscreen) {
                document.exitFullscreen();
            } else if (document.msExitFullscreen) {
                document.msExitFullscreen();
            } else if (document.mozCancelFullScreen) {
                document.mozCancelFullScreen();
            } else if (document.webkitExitFullscreen) {
                document.webkitExitFullscreen();
            }
        }
          },
        getRootLink: function (link) {
            var rtnValue = "";
            if (fullMenuRedirectionApplied == "true") {
                if (link) {
                    if (link.ROOT_URL != null && link.ROOT_URL.toLowerCase() == currentAppRootURL.toLowerCase()) {
                        rtnValue = "CURRENT";
                    }
                    else if (link.ROOT_URL != null) {
                        rtnValue = link.ROOT_URL;
                    }
                    else {
                        rtnValue = defaultAppURL;
                    }
                }
            }
            else {
                rtnValue = "CURRENT";
            }
            return rtnValue;
        },
        SetMenuType(type) {
            if (type == "L") {
                this.SelectedMenuType = "Left";
                $("body").addClass("menu-left-wrapper wrapper-shrink");
            }
            else {
                this.SelectedMenuType = "Top";
                $("body").removeClass("menu-left-wrapper wrapper-shrink");
            }
        },
        SetLanguage(code) {
            //v-model="$i18n.locale"
            this.$i18n.locale = code;
            var chkGroup = $.grep(this.Languages, function (element) {
                return element.LANGCODE == code;
            });
            this.SelectedLanguage = chkGroup[0].LANGNAME;
        },
        async GetLanguageList() {
            var vm = this;
            DataService.GetLanguageList().then((response) => {
                if (response.data.status == "Success") {
                    vm.Languages = response.data.result.DATA;
                    vm.getUnReadNotifications();
                }
            });
        },
        async GetCompanyLogo(imageName){
            var vm = this;
            var param='imagename='+imageName;
           await DataService.GetCompanyLogo(param).then((response) => {
                    vm.CompanyLogo = response.data;                
            });
        },
        async GetFULLMenuData() {
            var vm = this;
            DataService.GetFullMenu().then((response) => {
                if (response.data.status == "Success") {
                    vm.MenuData = response.data.result.DATA;
                    var MenuGroupData = [];
                    if (vm.MenuData != null && vm.MenuData.length > 0) {
                        var groupId = vm.MenuData[0].MODULE_MASTER_ID;
                        vm.MenuData.forEach(item => {
                            groupId = item.MODULE_MASTER_ID;
                            var chkGroup = $.grep(MenuGroupData, function (element) {
                                return element.ModuleMasterId == groupId;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(vm.MenuData, function (element) {
                                    return element.MODULE_MASTER_ID == groupId;
                                });
                                var chkDashBoard = $.grep(returnedData, function (element) {
                                    return String(element.ACTION_NAME).toLowerCase() == "dashboard";
                                });
                                var urlCount = returnedData.length;
                                var groupCount = Math.ceil(urlCount / 3);
                                var urlListData = [];
                                var urlIndex = 0;
                                returnedData.forEach(url => {
                                    var chkInsert = false;
                                    if (urlIndex == groupCount) {
                                        urlIndex = 0;
                                        chkInsert = true;
                                        var obj = {
                                            URL: []
                                        };
                                        obj.URL.push(url);
                                        urlListData.push(obj);
                                    }
                                    if (!chkInsert) {
                                        var length = urlListData.length;
                                        if (length == 0) {
                                            var obj1 = {
                                                URL: []
                                            };
                                            obj1.URL.push(url);
                                            urlListData.push(obj1);
                                        }
                                        else {
                                            urlListData[length - 1].URL.push(url);
                                        }
                                    }
                                    urlIndex += 1;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        ModuleMasterId: groupId,
                                        ModuleName: returnedData[0].MODULE_HEADER_NAME,
                                        ModuleIcon: returnedData[0].ATTACHMENT_PATH == "" ? "fa-cog" : returnedData[0].ATTACHMENT_PATH,
                                        ShowDashboardLink: chkDashBoard.length > 0 ? 1 : 0,
                                        ControllerName: chkDashBoard.length > 0 ? chkDashBoard[0].CONTROLER_NAME : "",
                                        ActionName: chkDashBoard.length > 0 ? chkDashBoard[0].ACTION_NAME : "",
                                        RoleApplicable: chkDashBoard.length > 0 ? chkDashBoard[0].ROLE_APPLICABLE : "",
                                        IsDisabled: chkDashBoard.length > 0 ? chkDashBoard[0].IS_DISABLED : "0",
                                        UrlList: urlListData
                                    };
                                    MenuGroupData.push(objGroup);
                                }
                            }
                        });
                        this.TotalCount = MenuGroupData.length;
                        this.TotalMenuCount = Math.ceil(this.TotalCount / 3);
                        this.MenuCountmodule = this.TotalCount % 3;
                        this.MenuDataList = MenuGroupData;
                        console.log(this.MenuDataList);
                        var chkMenuCount = 1;
                        var count = 1;
                        this.MenuDataGroupBy = [];
                        MenuGroupData.forEach(item => {
                            var countMenu = vm.TotalMenuCount;
                            if ((vm.MenuCountmodule <= 2 && chkMenuCount == 1) || (vm.MenuCountmodule == 2 && chkMenuCount == 2)) {
                                countMenu = countMenu + 1;
                            }
                            var chkInsert = false;
                            if (count == countMenu) {
                                count = 0;
                                chkMenuCount += 1;
                                if (chkMenuCount <= 3) {
                                    chkInsert = true;
                                    var obj = {
                                        Menu: []
                                    };
                                    obj.Menu.push(item);
                                    vm.MenuDataGroupBy.push(obj);
                                }
                            }
                            if (!chkInsert) {
                                var length = vm.MenuDataGroupBy.length;
                                if (length == 0) {
                                    var obj2 = {
                                        Menu: []
                                    };
                                    obj2.Menu.push(item);
                                    vm.MenuDataGroupBy.push(obj2);
                                }
                                else {
                                    vm.MenuDataGroupBy[length - 1].Menu.push(item);
                                }
                            }
                            count += 1;
                        });
                        setTimeout(function () {
                            vm.SetScrolbar("dark-thick", "", "navigation-panel-full-menu");
                        }, 1500);
                    }
                }
            });
        },
        GoToSubCategory(event, id) {
            var $this = $(event.target);
            $("#hdnischildsearch").val("1");
            ShowChild($this, false, id);
            $("#fullMenuUl").addClass("show");
        },
        SeacrchMenuItem(event) {
            var val = event.target.value;
            if (val != "") {
                $("a.clear-search").show();
            }
            else {
                $("a.clear-search").hide();
            }
            SearchMenuItem(val);
        },
        getUnReadNotifications() {
            var vm = this;
            DataService.UnreadItems("reqFrom=count").then(function (response) {
                vm.notifications = response.data;
            });
        },

        btnDeleteSample() {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmSampleDataDelete"), true, (vm.$t("Delete")), function () {

                DataService.DeleteSampleData().then(function (response) {

                    if (response.data.msg == "success") {
                        vm.ShowAlert(vm.$t("SampleDataDeleted"), "success", true, vm.$t("Alert"));
                    } else {
                        vm.ShowAlert(vm.$t("Something_Wrong"), "failure", true, vm.$t("Alert"));
                    }
                })

            })
        }
}
   
}
var timerOut;
var open_window = 0;
var chatlogwindow;
var isMobile;
